import React, { Component } from "react";
import "css/Community.css";
import { Article, Modal } from "components";
import interview from "data/community-interview";
import ImageGallery from "react-image-gallery";

const images = [
  {
    original: "https://picsum.photos/id/1018/1000/600/",
    thumbnail: "https://picsum.photos/id/1018/250/150/",
  },
  {
    original: "https://picsum.photos/id/1015/1000/600/",
    thumbnail: "https://picsum.photos/id/1015/250/150/",
  },
  {
    original: "https://picsum.photos/id/1019/1000/600/",
    thumbnail: "https://picsum.photos/id/1019/250/150/",
  },
];

class Community extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isModalOpen: false,
    };
  }

  openModal = (x) => {
    if (x != undefined) this.setState({ isModalOpen: true, dataId: x });
  };

  closeModal = () => {
    this.setState({ isModalOpen: false });
  };

  render() {
    return (
      <div className="wrapper">
        <div className="title-area" id="interview">
          <h2 className="en-title">Community</h2>
          <h2 className="ko-title">인터뷰</h2>
        </div>
        {interview.map((x, i) => (
          <div
            className="interview-section"
            key={`interview_${i}`}
            onClick={() => {
              this.openModal(x.id);
            }}
          >
            <img src={x.thumbnail} alt="" />
            <p className="interview-title">{x.title}</p>
            {/* <Article id={x.id} /> */}
            {/* <Modal isOpen={this.state.isModalOpen} close={this.closeModal} /> */}
          </div>
        ))}
        <Modal
          isOpen={this.state.isModalOpen}
          close={this.closeModal}
          data_id={this.state.dataId}
        />
        <div className="title-area" id="review">
          <h2 className="en-title">Community</h2>
          <h2 className="ko-title">리뷰</h2>
        </div>
        <div className="title-area" id="review">
          <h2 className="en-title">Community</h2>
          <h2 className="ko-title">갤러리</h2>
        </div>
        <div className="content-area" id="gallery">
          <ImageGallery items={images} />;
        </div>
      </div>
    );
  }
}

export default Community;
